import { NgModule } from '@angular/core';
import { HOOK_NAVIGATOR_NODES, HOOK_TABS } from '@c8y/ngx-components';
import { ActilityNodeFactory } from './actility-node.factory';
import { ActilityTabFactory } from './actility-tab.factory';
import { ActilityGuard } from './actility.guard';

@NgModule({
  providers: [
    ActilityGuard,
    ActilityTabFactory,
    {
      provide: HOOK_NAVIGATOR_NODES,
      useClass: ActilityNodeFactory,
      multi: true
    },
    {
      provide: HOOK_TABS,
      useClass: ActilityTabFactory,
      multi: true
    }
  ]
})
export class ActilityModule {}
