import { NgModule } from '@angular/core';
import { HOOK_NAVIGATOR_NODES, HOOK_TABS } from '@c8y/ngx-components';
import { SigfoxNodeFactory } from './sigfox-node.factory';
import { SigfoxTabFactory } from './sigfox-tab.factory';
import { SigfoxGuard } from './sigfox.guard';

@NgModule({
  providers: [
    SigfoxGuard,
    SigfoxTabFactory,
    {
      provide: HOOK_NAVIGATOR_NODES,
      useClass: SigfoxNodeFactory,
      multi: true
    },
    {
      provide: HOOK_TABS,
      useClass: SigfoxTabFactory,
      multi: true
    }
  ]
})
export class SigfoxModule {}
