import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';
import { isObservable, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { ImpactTabFactory } from './impact-tab.factory';

@Injectable()
export class ImpactNodeFactory implements NavigatorNodeFactory {
  private readonly connectivityNode: NavigatorNode = new NavigatorNode({
    parent: gettext('Settings'),
    label: gettext('Connectivity'),
    path: 'connectivitySettings',
    icon: 'plug',
    preventDuplicates: true
  });

  constructor(private tabFactory: ImpactTabFactory) {}

  get() {
    return this.wrapIntoObservable(this.tabFactory.tab()).pipe(
      map(tab => {
        return !!tab ? [this.connectivityNode] : [];
      })
    );
  }

  private wrapIntoObservable<T>(value: T | Observable<T>): Observable<T> {
    if (isObservable(value)) {
      return value;
    }

    return of(value);
  }
}
