import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { gettext, Tab, TabFactory } from '@c8y/ngx-components';
import { from, isObservable, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { ImpactGuard } from './impact.guard';

@Injectable()
export class ImpactTabFactory implements TabFactory {
  private config: Tab = {
    label: gettext('IMPACT provider settings'),
    path: '/connectivitySettings/impact_provider_settings',
    icon: 'plug'
  };

  constructor(private router: Router, private guard: ImpactGuard) {}

  get() {
    return this.router.url.match(/connectivitySettings/g) ? this.tab() : [];
  }

  tab(): Tab | Observable<Tab> {
    return this.wrapIntoObservable(
      this.guard.canActivate(
        this.router.routerState.snapshot.root,
        this.router.routerState.snapshot
      )
    ).pipe(map(canActivate => (canActivate ? this.config : null)));
  }

  private isPromise<T = any>(obj: any): obj is Promise<T> {
    return !!obj && typeof obj.then === 'function';
  }

  private wrapIntoObservable<T>(value: T | Promise<T> | Observable<T>): Observable<T> {
    if (isObservable(value)) {
      return value;
    }

    if (this.isPromise(value)) {
      return from(value);
    }

    return of(value);
  }
}
