import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { ConditionalTabsOutletComponent, CoreModule, Route } from '@c8y/ngx-components';
import { ActilityModule } from './actility/actility.module';
import { ImpactModule } from './impact/impact.module';
import { SigfoxModule } from './sigfox/sigfox.module';

const connectivityRoutes: Route[] = [
  {
    path: 'connectivitySettings',
    component: ConditionalTabsOutletComponent
  }
];

@NgModule({
  imports: [
    CoreModule,
    ActilityModule,
    ImpactModule,
    SigfoxModule,
    RouterModule.forChild(connectivityRoutes)
  ]
})
export class ConnectivityModule {}
