import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot } from '@angular/router';
import { ApplicationService } from '@c8y/client';
import { Permissions } from '@c8y/ngx-components';

@Injectable()
export class SimGuard implements CanActivate {
  private static readonly applicationName = 'connectivity';
  private showSimProvider: Promise<boolean>;

  constructor(private permissions: Permissions, private applicationService: ApplicationService) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean> {
    if (this.showSimProvider === undefined) {
      const hasRole: Promise<boolean> = Promise.resolve(
        this.permissions.hasRole('ROLE_CONNECTIVITY_READ')
      );

      this.showSimProvider = hasRole.then(
        (hasPermissions: boolean) =>
          hasPermissions &&
          this.applicationService
            .isAvailable(SimGuard.applicationName)
            .then(result => result && result.data)
      );
    }

    return this.showSimProvider;
  }
}
